import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Chat from './Chat';
import Profile from './Profile';
// import logo from '../public/logo192.png'; // Bunu kaldr

function App() {
  const [mode, setMode] = useState('login');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [displayName, setDisplayName] = useState('');
  const [message, setMessage] = useState('');
  const [token, setToken] = useState('');
  const [user, setUser] = useState(null);
  const [verifyCode, setVerifyCode] = useState('');
  const [pendingEmail, setPendingEmail] = useState('');
  const [contacts, setContacts] = useState([]);
  const [selectedContact, setSelectedContact] = useState(null);
  const [username, setUsername] = useState('');
  const [showAddContact, setShowAddContact] = useState(false);
  const [addEmail, setAddEmail] = useState('');
  const [addUsername, setAddUsername] = useState('');
  const [addContactMsg, setAddContactMsg] = useState('');

  // Oturum bilgisini localStorage'dan ykle
  useEffect(() => {
    const savedToken = localStorage.getItem('token');
    const savedUser = localStorage.getItem('user');
    if (savedToken && savedUser) {
      setToken(savedToken);
      setUser(JSON.parse(savedUser));
    }
  }, []);

  // Token veya user deiince localStorage'a kaydet
  useEffect(() => {
    if (token && user) {
      localStorage.setItem('token', token);
      localStorage.setItem('user', JSON.stringify(user));
    } else {
      localStorage.removeItem('token');
      localStorage.removeItem('user');
    }
  }, [token, user]);

  useEffect(() => {
    if (token) {
      axios.get('https://chubby-nails-stand.loca.lt/api/auth/contacts', {
        headers: { Authorization: `Bearer ${token}` }
      }).then(res => setContacts(res.data));
    }
  }, [token]);

  // Kii ekle
  const addContact = async ({ email, username }) => {
    try {
      let userRes;
      if (email) {
        userRes = await axios.get('https://chubby-nails-stand.loca.lt/api/auth/find', {
          params: { email },
          headers: { Authorization: `Bearer ${token}` }
        });
      } else if (username) {
        userRes = await axios.get('https://chubby-nails-stand.loca.lt/api/auth/find', {
          params: { username },
          headers: { Authorization: `Bearer ${token}` }
        });
      } else {
        return;
      }
      const contactId = userRes.data._id;
      await axios.post('https://chubby-nails-stand.loca.lt/api/auth/add-contact', { contactId }, {
        headers: { Authorization: `Bearer ${token}` }
      });
      // Kii listesini gncelle
      const contactsRes = await axios.get('https://chubby-nails-stand.loca.lt/api/auth/contacts', {
        headers: { Authorization: `Bearer ${token}` }
      });
      setContacts(contactsRes.data);
      // lk kii yoksa otomatik se
      if (!selectedContact && contactsRes.data.length > 0) setSelectedContact(contactsRes.data[0]);
    } catch {}
  };

  // Kii sil
  const deleteContact = async (contactId) => {
    try {
      await axios.post('https://chubby-nails-stand.loca.lt/api/auth/delete-contact', { contactId }, {
        headers: { Authorization: `Bearer ${token}` }
      });
      const updated = contacts.filter(c => c._id !== contactId);
      setContacts(updated);
      if (selectedContact && selectedContact._id === contactId) setSelectedContact(updated[0] || null);
    } catch {}
  };

  // Giri yaptktan sonra ilk kiiyi otomatik se
  useEffect(() => {
    if (contacts.length > 0 && !selectedContact) setSelectedContact(contacts[0]);
  }, [contacts, selectedContact]);

  const handleVerify = async (e) => {
    e.preventDefault();
    try {
      await axios.post('https://chubby-nails-stand.loca.lt/api/auth/verify-email', { email: pendingEmail, code: verifyCode });
      setMessage('E-posta doruland! imdi giri yapabilirsin.');
      setMode('login');
      setVerifyCode('');
      setPendingEmail('');
    } catch (err) {
      setMessage(err.response?.data?.message || 'Dorulama baarsz');
    }
  };

  const handleAuth = async (e) => {
    e.preventDefault();
    try {
      if (mode === 'register') {
        await axios.post('https://chubby-nails-stand.loca.lt/api/auth/register', { email, password, displayName, username });
        setMessage('Kayt baarl! Ltfen e-posta adresinize gelen kodu girin.');
        setPendingEmail(email);
        setMode('verify');
      } else {
        const res = await axios.post('https://chubby-nails-stand.loca.lt/api/auth/login', { email, password });
        setMessage('Giri baarl!');
        setToken(res.data.token);
        setUser({ ...res.data.user, id: res.data.user._id });
      }
    } catch (err) {
      setMessage(err.response?.data?.message || 'Bir hata olutu');
    }
  };

  // k fonksiyonu
  const handleLogout = () => {
    setToken('');
    setUser(null);
    setSelectedContact(null);
    localStorage.removeItem('token');
    localStorage.removeItem('user');
  };

  // Ayarlar butonu iin rnek fonksiyon
  const handleSettings = () => {
    alert('Ayarlar yaknda!');
  };

  // Kii ekle fonksiyonunu gncelle
  const handleAddContact = async (e) => {
    e.preventDefault();
    setAddContactMsg('');
    try {
      await addContact({ email: addEmail, username: addUsername });
      setAddEmail('');
      setAddUsername('');
      setAddContactMsg('Kii eklendi!');
      setTimeout(() => setShowAddContact(false), 800);
    } catch {
      setAddContactMsg('Kii eklenemedi!');
    }
  };

  // Eer token varsa sohbet ve profil ekrann gster
  if (token) {
    return (
      <div style={{ height: '100vh', display: 'flex', flexDirection: 'column' }}>
        {/* HEADER */}
        <div style={{
          width: '100%',
          background: '#222',
          color: '#fff',
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          padding: '0 32px',
          height: 60,
          minHeight: 60,
          boxSizing: 'border-box',
          boxShadow: '0 2px 8px #0002',
          zIndex: 10
        }}>
          <div style={{ display: 'flex', alignItems: 'center', gap: 16 }}>
            <img src="/logo192.png" alt="Verxiel Logo" style={{ width: 40, height: 40, borderRadius: 12, background: 'white', boxShadow: '0 1px 4px #0002' }} />
            <span style={{ fontWeight: 'bold', fontSize: 24, letterSpacing: 1 }}>Verxiel</span>
          </div>
          <div style={{ display: 'flex', alignItems: 'center', gap: 16 }}>
            <span style={{ fontSize: 16, fontWeight: 500 }}>{user?.displayName || user?.username || user?.email}</span>
            <button onClick={handleSettings} style={{ background: 'none', border: 'none', color: '#fff', fontSize: 20, cursor: 'pointer', marginRight: 8 }} title="Ayarlar"></button>
            <button onClick={handleLogout} style={{ background: '#ff4d4f', color: '#fff', border: 'none', borderRadius: 8, padding: '6px 16px', fontWeight: 'bold', cursor: 'pointer' }}>k</button>
          </div>
        </div>
        {/* ANA ERK */}
        <div style={{ display: 'flex', flex: 1, minHeight: 0 }}>
          <div className="app-sidebar" style={{ width: 220, borderRight: '1px solid #ccc', padding: 12, background: '#fafafa', minWidth: 120, position: 'relative' }}>
            <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', marginBottom: 8 }}>
              <h4 style={{ margin: 0 }}>Kiiler</h4>
              <button onClick={() => setShowAddContact(true)} style={{ background: '#a259e6', color: '#fff', border: 'none', borderRadius: '50%', width: 32, height: 32, fontSize: 22, fontWeight: 'bold', cursor: 'pointer', display: 'flex', alignItems: 'center', justifyContent: 'center' }} title="Kii Ekle">+</button>
            </div>
            <ul style={{ listStyle: 'none', padding: 0, margin: 0 }}>
              {contacts.map(c => (
                <li key={c._id} className={selectedContact?._id === c._id ? 'selected' : ''} style={{
                  margin: '8px 0',
                  cursor: 'pointer',
                  fontWeight: selectedContact?._id === c._id ? 'bold' : 'normal',
                  display: 'flex', alignItems: 'center', gap: 8,
                  borderRadius: 8, padding: 4
                }} onClick={() => setSelectedContact(c)}>
                  {c.avatarUrl ? (
                    <img src={c.avatarUrl} alt="avatar" style={{ width: 32, height: 32, borderRadius: '50%', objectFit: 'cover' }} />
                  ) : (
                    <div style={{ width: 32, height: 32, borderRadius: '50%', background: '#ccc', display: 'flex', alignItems: 'center', justifyContent: 'center', fontWeight: 'bold', fontSize: 16 }}>
                      {c.displayName?.[0]?.toUpperCase() || '?'}
                    </div>
                  )}
                  <span style={{ flex: 1 }}>{c.displayName}</span>
                </li>
              ))}
            </ul>
            {/* Kii ekle modal */}
            {showAddContact && (
              <div style={{
                position: 'fixed', top: 0, left: 0, width: '100vw', height: '100vh', background: '#0008', zIndex: 1000,
                display: 'flex', alignItems: 'center', justifyContent: 'center'
              }} onClick={() => setShowAddContact(false)}>
                <div style={{ background: '#fff', padding: 32, borderRadius: 16, minWidth: 320, boxShadow: '0 2px 16px #0003', position: 'relative' }} onClick={e => e.stopPropagation()}>
                  <button onClick={() => setShowAddContact(false)} style={{ position: 'absolute', top: 8, right: 12, background: 'none', border: 'none', fontSize: 22, cursor: 'pointer', color: '#888' }}></button>
                  <h3>Kii Ekle</h3>
                  <form onSubmit={handleAddContact} style={{ display: 'flex', gap: 8, flexDirection: 'column', marginTop: 12 }}>
                    <input value={addEmail} onChange={e => setAddEmail(e.target.value)} placeholder="E-posta ile kii ekle" style={{ width: '100%' }} />
                    <input value={addUsername} onChange={e => setAddUsername(e.target.value)} placeholder="Kullanc ad ile kii ekle" style={{ width: '100%' }} />
                    <button type="submit" style={{ background: '#a259e6', color: '#fff', border: 'none', borderRadius: 8, padding: '8px 0', fontWeight: 'bold', fontSize: 16 }}>Ekle</button>
                  </form>
                  <div style={{ marginTop: 8, color: addContactMsg === 'Kii eklendi!' ? 'green' : 'red' }}>{addContactMsg}</div>
                </div>
              </div>
            )}
          </div>
          <div style={{ flex: 1 }}>
            {selectedContact ? (
              <Chat token={token} user={user} contact={selectedContact} addContact={addContact} />
            ) : (
              <div style={{ margin: 40, color: '#888' }}>Bir kii sein...</div>
            )}
          </div>
          <div style={{ width: 320, borderLeft: '1px solid #ccc', padding: 12 }}>
            <Profile token={token} onContactsChange={setContacts} addContact={addContact} deleteContact={deleteContact} />
          </div>
        </div>
      </div>
    );
  }

  if (mode === 'verify') {
    return (
      <div style={{ maxWidth: 400, margin: 'auto', marginTop: 100 }}>
        <h2>E-posta Dorulama</h2>
        <form onSubmit={handleVerify}>
          <input
            type="text"
            placeholder="E-posta adresinize gelen kod"
            value={verifyCode}
            onChange={e => setVerifyCode(e.target.value)}
            required
            style={{ width: '100%', marginBottom: 8 }}
          />
          <button type="submit" style={{ width: '100%' }}>Dorula</button>
        </form>
        <div style={{ marginTop: 16, color: 'red' }}>{message}</div>
      </div>
    );
  }

  // Giri/kayt ekran
  return (
    <div style={{ maxWidth: 400, margin: 'auto', marginTop: 100 }}>
      <h2>{mode === 'login' ? 'Giri Yap' : 'Kayt Ol'}</h2>
      <form onSubmit={handleAuth}>
        <input
          type="email"
          placeholder="E-posta"
          value={email}
          onChange={e => setEmail(e.target.value)}
          required
          style={{ width: '100%', marginBottom: 8 }}
        />
        <input
          type="password"
          placeholder="ifre"
          value={password}
          onChange={e => setPassword(e.target.value)}
          required
          style={{ width: '100%', marginBottom: 8 }}
        />
        {mode === 'register' && (
          <>
            <input
              type="text"
              placeholder="Grnen Ad"
              value={displayName}
              onChange={e => setDisplayName(e.target.value)}
              required
              style={{ width: '100%', marginBottom: 8 }}
            />
            <input
              type="text"
              placeholder="Kullanc Ad"
              value={username}
              onChange={e => setUsername(e.target.value)}
              required
              style={{ width: '100%', marginBottom: 8 }}
            />
          </>
        )}
        <button type="submit" style={{ width: '100%' }}>
          {mode === 'login' ? 'Giri Yap' : 'Kayt Ol'}
        </button>
      </form>
      <button onClick={() => setMode(mode === 'login' ? 'register' : 'login')} style={{ marginTop: 8 }}>
        {mode === 'login' ? 'Hesabn yok mu? Kayt ol' : 'Zaten hesabn var m? Giri yap'}
      </button>
      <div style={{ marginTop: 16, color: 'red' }}>{message}</div>
    </div>
  );
}

export default App;
